import React, { useEffect, useState } from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import axios from 'axios';
import moment from 'moment';

// @material-ui/icons
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Typography from '@material-ui/core/Typography';
// core components
import Header from "components/Header/Header.js";
import Footer from "components/Footer/Footer.js";
import Button from "components/CustomButtons/Button.js";
import { ExitToApp } from "@material-ui/icons";

import styles from "assets/jss/material-kit-react/views/landingPage.js";
import styles2 from "assets/jss/material-kit-react/views/landingPageSections/productStyle.js";

// Sections for this page
import socketIOClient from "socket.io-client";

import { useHistory } from 'react-router-dom';

const dashboardRoutes = [];

const useStyles = makeStyles(styles, styles2);

export default function LandingPage(props) {
  const [orders, setOrders] = useState([]);
  const classes = useStyles();
  const navigate = useHistory();
  const [show, setShow] = useState(false);
  const { ...rest } = props;
  const socket = socketIOClient('http://146.59.151.193', {
    reconnection: false,
    path: '/pub/ws/socket/'
  });

  const initPage = async () => {
    const token = localStorage.getItem('token');
    try {
      await axios.get('http://146.59.151.193/pub/ws/backend/api/client/admin', {
        headers: {
          'Authorization': 'Bearer ' + token
        }
      });
      await fetchOrders();
      setShow(true)
    } catch (e) {
      navigate.push('/');
    }

  }

  const changeOrder = async (event, order) => {
    console.log(order, event.target.value)
    const token = localStorage.getItem('token');

    await axios.put('http://146.59.151.193/pub/ws/backend/api/order/status/' + order._id + '/' + event.target.value, null, {
      headers: {
        'Authorization': 'Bearer ' + token
      }
    });

    await fetchOrders();

    const response = await axios.get('http://146.59.151.193/pub/ws/backend/api/client/accountByUserId/' + order.ownerId, {
      headers: {
        'Authorization': 'Bearer ' + token
      }
    });
    const accountId = response.data._id;
    socket.emit('refreshThisRoom', accountId);
  }

  const fetchOrders = async () => {
    const token = localStorage.getItem('token');
    const response = await axios.get('http://146.59.151.193/pub/ws/backend/api/order/orders_V2', {
      headers: {
        'Authorization': 'Bearer ' + token
      }
    });
    let docs = response.data.docs.reverse();
    setOrders(docs);
  };

  useEffect(() => {
    socket.emit('joinRoom', 'admin');
    socket.on("refresh", () => {
      fetchOrders();
    });

    return () => socket.disconnect();
  }, [])

  useEffect(() => {
    initPage()
  }, [true])

  function onClickDeconnected() {
    localStorage.clear();
    navigate.push('/');
  }

  function onClickProduct() {
    navigate.push('/products');
  }

  return (!show) ? <p>Chargement</p> : (
    <div>
      <Header
        color="white"
        routes={dashboardRoutes}
        brand="Administration"
        rightLinks={<div>
          <Button
            onClick={onClickProduct}
            color="transparent"
            className={classes.navLink}
          >
            Produits
          </Button>
          <Button
            onClick={onClickDeconnected}
            color="transparent"
            className={classes.navLink}
          >
            <ExitToApp className={classes.icons} /> Deconnexion
          </Button></div>
        }
        fixed
        changeColorOnScroll={{
          height: 400,
          color: "white"
        }}
        {...rest}
      />

      <div className={classNames(classes.main)}>
        <div className={classes.container} style={{
          padding: "100px 0",
          textAlign: "center"
        }}>
          <List>
            {orders.map((e, i) => {
              return <ListItem key={i}>
                <ListItemText
                  primary={moment(e.createdDate).format("DD/MM/YYYY  hh:mm:ss")}
                  secondary={
                    <React.Fragment>
                      {
                        e.products.map((e, i) => {
                          return (e === null) ? 'Produit supprim' : <span key={i}>{e.name}<br /></span>
                        })
                      }
                      { e.comments }
                    </React.Fragment>
                  }
                />
                <ListItemSecondaryAction>
                  <FormControl variant="outlined">
                    <InputLabel htmlFor="outlined-age-native-simple">Status</InputLabel>
                    <Select
                      native
                      value={e.status}
                      onChange={(event) => changeOrder(event, e)}
                      label="Status"
                      inputProps={{
                        name: 'Status',
                        id: 'outlined-age-native-simple',
                      }}
                    >
                      <option value={0}>En attente de validation</option>
                      <option value={1}>En prparation</option>
                      <option value={2}>Finie</option>
                      <option value={3}>Annul</option>
                    </Select>
                  </FormControl>
                </ListItemSecondaryAction>
              </ListItem>
            })}
          </List>
        </div>
      </div>
      <Footer />
    </div>
  );
}
