import React, { useEffect, useState } from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import axios from 'axios';
import {DropzoneArea} from 'material-ui-dropzone'
import InputLabel from '@material-ui/core/InputLabel';
// @material-ui/icons
import TextField from '@material-ui/core/TextField';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import IconButton from '@material-ui/core/IconButton';
import Select from '@material-ui/core/Select';
// core components
import Header from "components/Header/Header.js";
import Footer from "components/Footer/Footer.js";
import Button from "components/CustomButtons/Button.js";
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import { ExitToApp } from "@material-ui/icons";
import MenuItem from '@material-ui/core/MenuItem';
import styles from "assets/jss/material-kit-react/views/landingPage.js";
import styles2 from "assets/jss/material-kit-react/views/landingPageSections/productStyle.js";
import ListSubheader from '@material-ui/core/ListSubheader';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

import { useHistory } from 'react-router-dom';

const dashboardRoutes = [];

const useStyles = makeStyles(styles, styles2);

export default function ProfilePage(props) {
  const [products, setProducts] = useState([]);
  const [file, setFile] = useState([]);
  const classes = useStyles();
  const navigate = useHistory();
  const [show, setShow] = useState(false);
  const [openRemoveDialog, setOpenRemoveDialog] = useState(false);
  const [openEditDialog, setOpenEditDialog] = useState(false);
  const [productToCache, setProductToCache] = useState({});
  const { ...rest } = props;

  const toBase64 = file => new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });

  const initPage = async () => {
    const token = localStorage.getItem('token');
    try {
      await axios.get('http://146.59.151.193/pub/ws/backend/api/client/admin', {
        headers: {
          'Authorization': 'Bearer ' + token
        }
      });
      await fetchProducts();
      setShow(true)
    } catch (e) {
      navigate.push('/');
    }

  }

  const fetchProducts = async () => {
    const token = localStorage.getItem('token');
    const response = await axios.get('http://146.59.151.193/pub/ws/backend/api/product/list', {
      headers: {
        'Authorization': 'Bearer ' + token
      }
    });
    let docs = response.data.rows;
    setProducts(docs);
  };

  const handleClickOpenEdit = async (id) => {
    const token = localStorage.getItem('token');
    if (id) {
      const response = await axios.get('http://146.59.151.193/pub/ws/backend/api/product/detail/' + id, {
        headers: {
          'Authorization': 'Bearer ' + token
        }
      });
      let docs = response.data;
      setProductToCache(docs);
    }
    setOpenEditDialog(true);
  }

  const handleCloseEdit = () => {
    setProductToCache({});
    setOpenEditDialog(false);
  };

  const handleApproveEdit = async () => {
    const token = localStorage.getItem('token');
    if (productToCache.hasOwnProperty('_id')) {
      await axios.put('http://146.59.151.193/pub/ws/backend/api/product/detail/' + productToCache._id, {
        name: productToCache.name,
        name2: productToCache.name2,
        description: productToCache.description,
        category: productToCache.category,
        price: productToCache.price,
        image: productToCache.image,
        quantity: productToCache.quantity
      }, {
        headers: {
          'Authorization': 'Bearer ' + token
        }
      });
    } else {
      await axios.post('http://146.59.151.193/pub/ws/backend/api/product/detail', {
        name: productToCache.name,
        name2: productToCache.name2,
        description: productToCache.description,
        price: productToCache.price,
        category: productToCache.category,
        image: productToCache.image,
        quantity: productToCache.quantity
      }, {
        headers: {
          'Authorization': 'Bearer ' + token
        }
      });
    }
    
    setProductToCache({});
    await fetchProducts();
    setOpenEditDialog(false);
  }

  const handleClickOpenRemove = async (id) => {
    const token = localStorage.getItem('token');
    const response = await axios.get('http://146.59.151.193/pub/ws/backend/api/product/detail/' + id, {
      headers: {
        'Authorization': 'Bearer ' + token
      }
    });
    let docs = response.data;
    setProductToCache(docs);
    setOpenRemoveDialog(true);
  };
  
  const handleApproveRemove = async () => {
    const token = localStorage.getItem('token');
    await axios.delete('http://146.59.151.193/pub/ws/backend/api/product/detail/' + productToCache._id, {
      headers: {
        'Authorization': 'Bearer ' + token
      }
    });
    
    setProductToCache({});
    await fetchProducts();
    setOpenRemoveDialog(false);
  }

  const handleCloseRemove = () => {
    setProductToCache({});
    setOpenRemoveDialog(false);
  };

  const changeImage = async (e) => {
    if (e.length > 0) {
      let t = await toBase64(e[e.length - 1]);
      t = t.split('base64,')[1];
      setProductToCache({...productToCache, image: t});
    }
  }

  useEffect(() => {
    initPage()
  }, [true])

  function onClickDeconnected() {
    localStorage.clear();
    navigate.push('/');
  }

  function onClickCommand() {
    navigate.push('/landing-page');
  }

  return (!show) ? <p>Chargement</p> : (
    <div>
      <Header
        color="white"
        routes={dashboardRoutes}
        brand="Administration"
        rightLinks={<div>
          <Button
            onClick={onClickCommand}
            color="transparent"
            className={classes.navLink}
          >
            Commandes
          </Button>
          <Button
            onClick={onClickDeconnected}
            color="transparent"
            className={classes.navLink}
          >
            <ExitToApp className={classes.icons} /> Deconnexion
          </Button></div>
        }
        fixed
        changeColorOnScroll={{
          height: 400,
          color: "white"
        }}
        {...rest}
      />

      <div className={classNames(classes.main)}>
        <div className={classes.container} style={{
          padding: "100px 0",
          textAlign: "center"
        }}>
          <List>
            <ListSubheader>Menu</ListSubheader>
            {products.map((e, i) => {
              if (e.doc.category === 'Menu')
                return <ListItem key={i}>
                  <ListItemText
                    primary={e.doc.name}
                    secondary={e.doc.description + '\n' + e.doc.price + ' '}
                  />
                  <ListItemSecondaryAction>
                    <IconButton edge="end" aria-label="edit" onClick={() => handleClickOpenEdit(e.doc._id)}>
                      <EditIcon />
                    </IconButton>
                    <IconButton edge="end" aria-label="delete" onClick={() => handleClickOpenRemove(e.doc._id)}>
                      <DeleteIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
            })}
            <ListSubheader>Entre</ListSubheader>
            {products.map((e, i) => {
              if (e.doc.category === 'Entre')
                return <ListItem key={i}>
                  <ListItemText
                    primary={e.doc.name}
                    secondary={e.doc.description + '\n' + e.doc.price + ' '}
                  />
                  <ListItemSecondaryAction>
                    <IconButton edge="end" aria-label="edit" onClick={() => handleClickOpenEdit(e.doc._id)}>
                      <EditIcon />
                    </IconButton>
                    <IconButton edge="end" aria-label="delete" onClick={() => handleClickOpenRemove(e.doc._id)}>
                      <DeleteIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
            })}
            <ListSubheader>Plat</ListSubheader>
            {products.map((e, i) => {
              if (e.doc.category === 'Plat')
                return <ListItem key={i}>
                  <ListItemText
                    primary={e.doc.name}
                    secondary={e.doc.description + '\n' + e.doc.price + ' '}
                  />
                  <ListItemSecondaryAction>
                    <IconButton edge="end" aria-label="edit" onClick={() => handleClickOpenEdit(e.doc._id)}>
                      <EditIcon />
                    </IconButton>
                    <IconButton edge="end" aria-label="delete" onClick={() => handleClickOpenRemove(e.doc._id)}>
                      <DeleteIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
            })}
            <ListSubheader>Soupe</ListSubheader>
            {products.map((e, i) => {
              if (e.doc.category === 'Soupe')
                return <ListItem key={i}>
                  <ListItemText
                    primary={e.doc.name}
                    secondary={e.doc.description + '\n' + e.doc.price + ' '}
                  />
                  <ListItemSecondaryAction>
                    <IconButton edge="end" aria-label="edit" onClick={() => handleClickOpenEdit(e.doc._id)}>
                      <EditIcon />
                    </IconButton>
                    <IconButton edge="end" aria-label="delete" onClick={() => handleClickOpenRemove(e.doc._id)}>
                      <DeleteIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
            })}
            <ListSubheader>Dessert</ListSubheader>
            {products.map((e, i) => {
              if (e.doc.category === 'Dessert')
                return <ListItem key={i}>
                  <ListItemText
                    primary={e.doc.name}
                    secondary={e.doc.description + '\n' + e.doc.price + ' '}
                  />
                  <ListItemSecondaryAction>
                    <IconButton edge="end" aria-label="edit" onClick={() => handleClickOpenEdit(e.doc._id)}>
                      <EditIcon />
                    </IconButton>
                    <IconButton edge="end" aria-label="delete" onClick={() => handleClickOpenRemove(e.doc._id)}>
                      <DeleteIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
            })}
            <ListSubheader>Boisson</ListSubheader>
            {products.map((e, i) => {
              if (e.doc.category === 'Boisson')
                return <ListItem key={i}>
                  <ListItemText
                    primary={e.doc.name}
                    secondary={e.doc.description + '\n' + e.doc.price + ' '}
                  />
                  <ListItemSecondaryAction>
                    <IconButton edge="end" aria-label="edit" onClick={() => handleClickOpenEdit(e.doc._id)}>
                      <EditIcon />
                    </IconButton>
                    <IconButton edge="end" aria-label="delete" onClick={() => handleClickOpenRemove(e.doc._id)}>
                      <DeleteIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
            })}
          </List>
        </div>
      </div>
      <Fab style={{
        position: 'absolute',
        right: 50,
        bottom: 50
      }} color="primary" aria-label="add" onClick={() => handleClickOpenEdit(null)}>
        <AddIcon />
      </Fab>
      <Dialog
        open={openRemoveDialog}
        onClose={handleCloseRemove}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Attention"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Ce produit sera dfinitivement supprim ?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseRemove} color="primary">
            Annuler
          </Button>
          <Button onClick={handleApproveRemove} color="primary" autoFocus>
            Accepter
          </Button>
        </DialogActions>
      </Dialog>
      <Dialog open={openEditDialog} onClose={handleCloseEdit} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Produit</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Informations du produit
          </DialogContentText>
          <TextField
            margin="dense"
            id="name"
            label="Nom du produit"
            defaultValue={productToCache.name}
            onChange = {e => setProductToCache({...productToCache, name: e.target.value})}
            fullWidth
          />
          <TextField
            margin="dense"
            id="name2"
            label="Nom malgache du produit"
            defaultValue={productToCache.name2}
            onChange = {e => setProductToCache({...productToCache, name2: e.target.value})}
            fullWidth
          />
          <TextField
            margin="dense"
            id="description"
            label="Description du produit"
            defaultValue={productToCache.description}
            onChange = {e => setProductToCache({...productToCache, description: e.target.value})}
            multiline
            rows={2}
            fullWidth
          />
          <InputLabel shrink id="labelCategorie">Catgorie</InputLabel>
          <Select
            labelId="labelCategorie"
            value={productToCache.category}
            label="Catgorie"
            onChange = {e => setProductToCache({...productToCache, category: e.target.value})}
          >
            <MenuItem value={'Menu'}>Menu</MenuItem>
            <MenuItem value={'Entre'}>Entre</MenuItem>
            <MenuItem value={'Plat'}>Plat</MenuItem>
            <MenuItem value={'Soupe'}>Soupe</MenuItem>
            <MenuItem value={'Dessert'}>Dessert</MenuItem>
            <MenuItem value={'Boisson'}>Boisson</MenuItem>
          </Select>
          <TextField
            defaultValue={productToCache.price}
            margin="dense"
            id="price"
            label="Prix"
            type="number"
            onChange = {e => setProductToCache({...productToCache, price: parseInt(e.target.value)})}
            fullWidth
          />
          <TextField
            defaultValue={productToCache.quantity}
            margin="dense"
            id="quantity"
            label="Quantit"
            type="number"
            onChange = {e => setProductToCache({...productToCache, quantity: parseInt(e.target.value)})}
            fullWidth
          /><br />
          <DropzoneArea
            onChange={e => changeImage(e)}
            showPreviews={false}
            showPreviewsInDropzone={false}
          />
        </DialogContent>
        {
          productToCache.hasOwnProperty('image')
          ? <div style={{textAlign: 'center'}}>
              <img src={'data:image/jpeg;base64,' + productToCache.image} style={{width: '40%', height: '40%'}} />
            </div>
          : null
        }
        <DialogActions>
          <Button onClick={handleCloseEdit} color="primary">
            Annuler
          </Button>
          <Button onClick={handleApproveEdit} color="primary">
            Enregistrer
          </Button>
        </DialogActions>
      </Dialog>
      <Footer />
    </div>
  );
}
